import { contact } from '../../portfolio'
import './Contact.css'

const Contact = () => {
  if (!contact.email) return null

  return (
    <section className='section contact center' id='contact'>
      <h2 className='section__title'>Contact</h2>

      <div className='contact__links'>

        {/* Email */}
        <a href={`mailto:${contact.email}`} className='contact__item'>
          <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
            <path d="M4 4h16v16H4V4zm8 7L4 6v12h16V6l-8 5z" fill="currentColor" />
          </svg>
          <span>Email</span>
        </a>

        {/* LinkedIn */}
        {contact.Linkedin && (
          <a
            href={contact.Linkedin}
            target="_blank"
            rel="noreferrer"
            className='contact__item'
          >
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
              <path d="M4.98 3a2 2 0 110 4 2 2 0 010-4zM4 8h2v12H4V8zm7 0h2v1.8h.03c.28-.54 1-1.8 3-1.8 3.21 0 3.97 2.11 3.97 4.86V20h-2v-6.9c0-1.65-.03-3.8-2.31-3.8-2.31 0-2.67 1.8-2.67 3.66V20h-2V8z" fill="currentColor"/>
            </svg>
            <span>LinkedIn</span>
          </a>
        )}

        {/* Twitter (X) */}
        {contact.Twitter && (
          <a
            href={contact.Twitter}
            target="_blank"
            rel="noreferrer"
            className='contact__item'
          >
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
              <path d="M4 4l8 8-8 8h3l8-8-8-8H4zm13 0l-3 3 5 5-5 5 3 3 8-8-8-8z" fill="currentColor"/>
            </svg>
            <span>Twitter</span>
          </a>
        )}

        {/* Téléphone */}
        {contact.Phone && (
          <a href={`tel:${contact.Phone}`} className='contact__item'>
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
              <path d="M6.6 10.8A11.05 11.05 0 0013.2 17.4l2.1-2.1c.3-.3.8-.4 1.2-.3 1.3.4 2.7.6 4.2.6.7 0 1.3.6 1.3 1.3V22c0 .7-.6 1.3-1.3 1.3C10.1 23.3.7 13.9.7 2.6.7 1.9 1.3 1.3 2 1.3H6c.7 0 1.3.6 1.3 1.3 0 1.5.2 2.9.6 4.2.1.4 0 .9-.3 1.2l-2 2.8z" fill="currentColor"/>
            </svg>
            <span>Téléphone</span>
          </a>
        )}

      </div>
    </section>
  )
}

export default Contact
